/* Expenditures.css */

div.p-4 {
  max-width: 700px;
  margin: 3rem auto;
  padding: 2rem;
  background-color: #fffefc;
  border-radius: 10px;
  box-shadow: 0 6px 14px rgba(0,0,0,0.1);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

h2.text-xl {
  color: #2f4f4f;
  margin-bottom: 1.5rem;
  text-align: center;
}

form.space-y-2 {
  display: flex;
  flex-direction: column;
}

form.space-y-2 input {
  padding: 0.7rem 1rem;
  margin-bottom: 1rem;
  font-size: 1rem;
  border: 1.5px solid #ccc;
  border-radius: 6px;
  transition: border-color 0.3s ease;
}

form.space-y-2 input:focus {
  border-color: #3182ce;
  outline: none;
}

form.space-y-2 button {
  background-color:#004494;
  color: white;
  padding: 0.75rem 0;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

form.space-y-2 button:hover {
  background-color: #255eaa;
}

ul {
  margin-top: 2rem;
  list-style-type: none;
  padding-left: 0;
}

ul li {
  background-color: #f4f7f8;
  margin-bottom: 0.8rem;
  padding: 0.8rem 1rem;
  border-radius: 6px;
  border: 1px solid #ddd;
  color: #333;
  font-weight: 500;
  transition: background-color 0.3s ease;
}

ul li:hover {
  background-color: #e2e8f0;
}
